import React, { Component } from 'react';

class ShareBar extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div id="section-share">
            	<span id="text-shareheading">Share this app!</span>
            	<a className="button-share" href="#"><i class="fab fa-facebook" /></a>
            	<a className="button-share" href="#"><i class="fab fa-twitter-square" /></a>
            </div>
        );
    }
}

export default ShareBar;
